import React, { useState, useCallback, useMemo } from 'react';
// FIX: Updated import extension to .jsx
import { useAuth } from '../context/AuthContext.jsx'; 
import { getFunctions, httpsCallable } from 'firebase/functions';

// Utility to implement exponential backoff for retries
const callFunctionWithBackoff = async (callable, data, maxRetries = 3, initialDelay = 1000) => {
    let delay = initialDelay;
    for (let i = 0; i < maxRetries; i++) {
        try {
            const response = await callable(data);
            return response; 
        } catch (error) {
            if (i < maxRetries - 1) {
                console.warn(`Function call failed. Retrying in ${delay}ms...`);
                await new Promise(resolve => setTimeout(resolve, delay));
                delay *= 2; 
            } else {
                throw error; 
            }
        }
    }
};

const Scanner = () => {
    const { currentUser, auth } = useAuth();
    const [metadata, setMetadata] = useState(null);
    const [error, setError] = useState('');
    const [manualLoading, setManualLoading] = useState(false);
    const [aiLoading, setAiLoading] = useState(false);
    const [imageFile, setImageFile] = useState(null);
    const [imagePreviewUrl, setImagePreviewUrl] = useState('');

    // Access Firebase Functions
    const functions = useMemo(() => {
        if (!auth.app) return null;
        return getFunctions(auth.app, 'us-central1');
    }, [auth.app]);

    // Converts a File object to a Base64 string
    const fileToBase64 = (file) => {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.onload = () => resolve(reader.result);
            reader.onerror = error => reject(error);
            reader.readAsDataURL(file);
        });
    };

    // 1. Manual Barcode Lookup Handler
    const handleManualLookup = useCallback(async (e) => {
        e.preventDefault();
        if (!functions) {
            setError("Firebase Functions not initialized.");
            return;
        }

        const formData = new FormData(e.target);
        const titleCode = formData.get('title-code').trim();
        const issueNumber = parseInt(formData.get('issue-number'), 10);
        const coverVariant = formData.get('cover-variant').trim() || 'A';
        
        if (!titleCode || isNaN(issueNumber)) {
            setError("Please enter a valid Title Code and Issue Number.");
            return;
        }

        setManualLoading(true);
        setError('');
        setMetadata(null);
        
        try {
            const fetchComicMetadata = httpsCallable(functions, 'fetchComicMetadata');
            const response = await callFunctionWithBackoff(fetchComicMetadata, {
                titleCode: titleCode,
                issueNumber: issueNumber,
                coverVariant: coverVariant
            });
            
            if (response.data.status === 'success') {
                setMetadata(response.data.metadata);
            } else {
                setError(response.data.message || 'Manual Lookup Failed.');
            }

        } catch (err) {
            console.error("Manual Lookup Function Call Error:", err);
            setError(`Lookup Failed: ${err.message}. Check console for details.`);
        } finally {
            setManualLoading(false);
        }
    }, [functions]);

    // 2. AI Image Analysis Handler
    const handleAIAnalysis = useCallback(async (e) => {
        e.preventDefault();
        if (!functions) {
            setError("Firebase Functions not initialized.");
            return;
        }

        if (!imageFile) {
            setError("Please select an image file to analyze.");
            return;
        }
        
        setAiLoading(true);
        setError('');
        setMetadata(null);
        
        try {
            const base64Image = await fileToBase64(imageFile);
            
            const processImageForMetadata = httpsCallable(functions, 'processImageForMetadata');
            const response = await callFunctionWithBackoff(processImageForMetadata, {
                base64Image: base64Image.split(',')[1],
                imageMimeType: imageFile.type
            });
            
            if (response.data.status === 'success') {
                const extracted = response.data.extractedData;
                
                const fetchComicMetadata = httpsCallable(functions, 'fetchComicMetadata');
                const secondResponse = await callFunctionWithBackoff(fetchComicMetadata, {
                    titleCode: extracted.titleCode,
                    issueNumber: extracted.issueNumber,
                    coverVariant: extracted.coverVariant || 'A'
                });

                if (secondResponse.data.status === 'success') {
                    setMetadata(secondResponse.data.metadata);
                } else {
                    setError(`AI successfully extracted: ${extracted.message}. But metadata lookup failed: ${secondResponse.data.message}`);
                }
                
            } else {
                setError(response.data.message || 'AI Analysis Failed.');
            }

        } catch (err) {
            console.error("AI Analysis Function Call Error:", err);
            setError(`Image Analysis Failed: ${err.message}. Check console for details.`);
        } finally {
            setAiLoading(false);
        }
    }, [functions, imageFile]);


    const handleImageChange = (e) => {
        const file = e.target.files[0];
        if (imagePreviewUrl) URL.revokeObjectURL(imagePreviewUrl);

        if (file) {
            setImageFile(file);
            setImagePreviewUrl(URL.createObjectURL(file));
            setError('');
        } else {
            setImageFile(null);
            setImagePreviewUrl('');
        }
    };


    const renderMetadata = (data) => {
        const details = data.details || {};
        const creators = data.creators || {};
        
        const formatCreators = (role) => creators[role] && creators[role].length > 0
            ? creators[role].join(', ')
            : 'N/A';

        return (
            <div className="flex flex-col md:flex-row gap-6">
                <div className="md:w-1/3">
                    <img 
                        src={details.image_url} 
                        alt={`${details.series_title} #${details.issue_number}`} 
                        className="rounded-lg shadow-lg w-full h-auto object-cover max-h-96" 
                        onError={(e) => { e.target.onerror = null; e.target.src='https://placehold.co/200x300/e5e7eb/7f8c8d?text=No+Image'; }}
                    />
                    <p className="text-xs text-gray-400 text-center mt-2">Source: {details.imageSource || 'Unknown'}</p>
                </div>
                <div className="md:w-2/3 space-y-3">
                    <h3 className="text-2xl font-bold text-emerald-700">{details.series_title} #{details.issue_number}</h3>
                    <p className="text-lg text-gray-800 font-semibold">{details.publisher_name}</p>
                    <hr className="border-gray-100" />
                    <div className="text-sm">
                        <p><span className="font-medium text-gray-700">Release:</span> {details.release_date || 'N/A'}</p>
                        <p><span className="font-medium text-gray-700">SKU Base:</span> {details.base_sku || 'N/A'}</p>
                        <p><span className="font-medium text-gray-700">Volume ID:</span> {details.volume_id || 'N/A'}</p>
                    </div>
                    <hr className="border-gray-100" />
                    <div className="text-sm space-y-1">
                        <p className="font-medium text-gray-700">Writer: <span className="font-normal text-gray-600">{formatCreators('writer')}</span></p>
                        <p className="font-medium text-gray-700">Penciller: <span className="font-normal text-gray-600">{formatCreators('penciller')}</span></p>
                        <p className="font-medium text-gray-700">Colorist: <span className="font-normal text-gray-600">{formatCreators('colorist')}</span></p>
                    </div>
                </div>
            </div>
        );
    };

    const userId = currentUser?.uid || 'N/A';

    return (
        <div className="min-h-screen bg-gray-50 p-4 md:p-8">
            <div className="max-w-6xl mx-auto space-y-8">
                
                <header className="flex justify-between items-center border-b pb-4 bg-white p-4 rounded-xl shadow-md">
                    <h1 className="text-3xl font-extrabold text-gray-800">Comic Scan Pro <span className="text-emerald-600 text-xl font-medium">Scanner</span></h1>
                    <div className="flex items-center space-x-3">
                        <span className="text-sm text-gray-500 hidden md:block">{currentUser?.email || 'Anonymous'}</span>
                        <button 
                            onClick={logout} 
                            className="px-4 py-2 bg-red-600 text-white font-medium rounded-lg shadow-md hover:bg-red-700 transition duration-150"
                        >
                            Log Out
                        </button>
                    </div>
                </header>
                
                {error && (
                    <div className="p-4 bg-red-100 text-red-700 font-medium rounded-xl shadow-sm">
                        {error}
                    </div>
                )}

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                    {/* 1. Manual Barcode Lookup */}
                    <div className="border border-gray-200 rounded-xl p-6 space-y-4 shadow-lg bg-white">
                        <h2 className="text-xl font-bold text-gray-700 border-b pb-2 mb-2 flex items-center">
                            Manual Lookup (Barcode) 
                            <span className="text-xs font-normal text-gray-400 ml-2">(Primary Method)</span>
                        </h2>
                        <form onSubmit={handleManualLookup} className="space-y-4">
                            <div className="grid grid-cols-3 gap-3">
                                <div>
                                    <label htmlFor="title-code" className="block text-xs font-medium text-gray-700">Title Code</label>
                                    <input type="text" id="title-code" name="title-code" defaultValue="00102" required 
                                        className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm p-3 text-sm border focus:ring-emerald-500 focus:border-emerald-500" 
                                        placeholder="e.g., 00102" disabled={manualLoading || aiLoading} />
                                </div>
                                <div>
                                    <label htmlFor="issue-number" className="block text-xs font-medium text-gray-700">Issue #</label>
                                    <input type="number" id="issue-number" name="issue-number" defaultValue="1" required 
                                        className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm p-3 text-sm border focus:ring-emerald-500 focus:border-emerald-500" 
                                        placeholder="e.g., 1" disabled={manualLoading || aiLoading} />
                                </div>
                                <div>
                                    <label htmlFor="cover-variant" className="block text-xs font-medium text-gray-700">Variant</label>
                                    <input type="text" id="cover-variant" name="cover-variant" defaultValue="A" 
                                        className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm p-3 text-sm border focus:ring-emerald-500 focus:border-emerald-500" 
                                        placeholder="e.g., A" disabled={manualLoading || aiLoading} />
                                </div>
                            </div>
                            <button type="submit" disabled={manualLoading || aiLoading}
                                    className="w-full flex items-center justify-center py-3 px-4 border border-transparent rounded-lg shadow-md text-base font-medium text-white bg-emerald-600 hover:bg-emerald-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-emerald-500 transition duration-150 ease-in-out disabled:opacity-50">
                                {manualLoading ? (
                                    <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"><circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle><path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path></svg>
                                ) : 'Lookup Metadata'}
                            </button>
                        </form>
                    </div>

                    {/* 2. AI Image Analysis */}
                    <div className="border border-gray-200 rounded-xl p-6 space-y-4 shadow-lg bg-white">
                        <h2 className="text-xl font-bold text-gray-700 border-b pb-2 mb-2 flex items-center">
                            AI Image Analysis 
                            <span className="text-xs font-normal text-gray-400 ml-2">(Fallback Method)</span>
                        </h2>
                        <form onSubmit={handleAIAnalysis} className="space-y-4">
                            <label htmlFor="image-upload" className="block text-sm font-medium text-gray-700">Upload Comic Cover (JPG/PNG)</label>
                            <input 
                                type="file" id="image-upload" accept="image/jpeg, image/png" required
                                onChange={handleImageChange}
                                className="mt-1 block w-full text-sm text-gray-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-green-50 file:text-green-700 hover:file:bg-green-100"
                                disabled={manualLoading || aiLoading}
                            />

                            {/* Image Preview */}
                            {imagePreviewUrl && (
                                <div className="mt-4 border border-gray-100 p-2 rounded-lg bg-gray-50">
                                    <p className="text-xs text-gray-500 mb-1">Preview:</p>
                                    <img id="image-preview" src={imagePreviewUrl} className="max-h-48 w-auto mx-auto rounded-lg shadow-md" alt="Image Preview" />
                                </div>
                            )}

                            <button type="submit" disabled={manualLoading || aiLoading || !imageFile}
                                    className="w-full flex items-center justify-center py-3 px-4 border border-transparent rounded-lg shadow-md text-base font-medium text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transition duration-150 ease-in-out disabled:opacity-50">
                                {aiLoading ? (
                                    <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"><circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle><path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path></svg>
                                ) : 'Analyze Image'}
                            </button>
                        </form>
                    </div>
                </div>

                {/* Metadata Result Display */}
                <div id="metadata-result" className="mt-8 pt-6 border-t border-gray-200 bg-white p-6 rounded-xl shadow-lg">
                    <h2 className="text-xl font-bold text-gray-700 mb-4">Metadata Results</h2>
                    <div id="results-content" className="space-y-4">
                        {metadata ? (
                            renderMetadata(metadata)
                        ) : (
                            <p className="text-gray-500 italic">Metadata will appear here after a successful lookup.</p>
                        )}
                    </div>
                </div>

                <div className="mt-6 pt-4 border-t border-gray-100 text-center">
                    <p className="text-xs text-gray-400">Current User ID: <span className="font-mono">{userId}</span></p>
                </div>
            </div>
        </div>
    );
};

export default Scanner;